import React from 'react'
import  { Component } from 'react';
import { connect } from 'react-redux';
import moment from 'moment';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import logo from '../components/logo.png'

import { add_reminder,remove_reminder,clear_reminder } from '../action';

class App extends Component{
    state={
        text:'',
        date:new Date()
    }

    render_Reminder = () =>{
        const {reminders} = this.props;

        return (
            <ul className = 'list-group'>
                {
                    reminders.map(reminder=>{
                        return (
                            <li key={reminder.id }className='list-group-item'>
                                <div>{reminder.text}</div>
                                <div>{moment(new Date(reminder.date)).fromNow()}</div>
                                <div className='remove closeIcon' onClick={()=>this.props.remove_reminder(reminder.id)}>X</div>

                               </li>

                        )
                    })
                }

            </ul>
        )
    }
    render(){
        return(
            <div className ="app">
                <img src={logo}/>
                <div className = 'reminder-title'>
                <h1>What Should I Do ?</h1>
                </div>
                <input
                className ='form-control'
                type ='text'
                placeholder='Enter What You Want ....?'
                onChange={(e)=>this.setState({text:e.target.value})}
                value={this.state.text}/>
                
                    <DatePicker
                     className = 'form-control'
                     value={this.state.date}
                    selected={this.state.date}
                    onChange={(date)=>{this.setState({date})}}
                    showTimeSelect
                    timeFormat ="HH:mm"
                    dateFormat="MMMM d ,yyyy :h:m aa"
                    timeCaption="Time"
                    placeholder  ='Enter Date'
                    />
                <button className='btn btn-primary col-12 m-1'
                onClick={()=>{
                    this.props.add_reminder(this.state.text,this.state.date)
                    this.setState({text:'' ,date:''})}}>Add Reminder</button>
                {this.render_Reminder()}
                <button className= 'btn btn-danger col-12 m-1 clearReminder'
                onClick={()=>this.props.clear_reminder()}>Clear Reminder</button>
                
            </div>
        )
    }
}

